import classes from "./Scheduled.module.css";
import React from "react";
import cancel from "../../../assets/icons-2/cancel-white.svg";
import CustomButton from "../../UI/Button/CustomButton";

const Scheduled = ({ image }) => {
  return (
    <>
      {/* <Backdrop /> */}
      <div className={classes.Scheduled}>
        <div className={classes.Header}>
          <h1>Scheduled</h1>
          <img
            onClick={() => window.location.reload()}
            src={cancel}
            alt="cancel"
          />
        </div>

        <div className={classes.Post_Container}>
          <img src={URL.createObjectURL(image)} alt="post" />
        </div>

        <div className={classes.Text_Container}>
          <h2>Your post has been scheduled</h2>
          <CustomButton
            className={classes.CustomButton}
            onClick={() => {
              let link = document.createElement("a");
              link.href = URL.createObjectURL(image);
              link.download = "easy-social-post.jpg";
              document.body.appendChild(link);
              link.click();
              document.body.removeChild(link);
            }}
          >
            Download image
          </CustomButton>
          <p>
            Sharing the post on WhatsApp can bring upto 5x better visibility
          </p>
        </div>
      </div>
    </>
  );
};

export default Scheduled;
