import DateFnsUtils from "@date-io/date-fns";
import {
  DateTimePicker,
  MuiPickersUtilsProvider,
  TimePicker,
} from "@material-ui/pickers";
import { motion } from "framer-motion";
import React, { useCallback, useState } from "react";
import Header from "../../Components/Dashboard/Header/Header";
import classes from "./CreatePost.module.css";
import facebook from "../../assets/social-media/facebook.svg";
import instagram from "../../assets/social-media/instagram (1).svg";
import twitter from "../../assets/social-media/twitter.svg";
import linkedIn from "../../assets/social-media/linkedin (1).svg";
import setting from "../../assets/icons/settings.svg";
import alarm from "../../assets/icons/alarm.png";
import { Checkbox } from "@material-ui/core";
import Axios from "axios";
import upArrow from "../../assets/icons/up-arrow.png";
import { Line } from "rc-progress";
import Backdrop from "../../Components/UI/Backdrop/Backdrop";
import Scheduled from "../../Components/CreatePost/Scheduled/Scheduled";
import { useToastMessage } from "../../contexts/ToastMessageContext";
import { API } from "../../Backend";

const CreatePost = (props) => {
  const { setErrorMessage } = useToastMessage();

  const [loading, setLoading] = useState({
    show: false,
    percentage: 0,
    success: false,
  });

  const [twitterInput, showTwitterInput] = useState(false);

  const [postData, setPostData] = useState({
    advance: false,
    postDestination: {
      facebook: true,
      twitter: true,
      linkedIn: true,
      instagram: true,
    },
    twitterCopy: "",
    copyData: "",
    imageData: null,
    scheduledAt: {
      facebook: new Date(),
      twitter: new Date(),
      linkedIn: new Date(),
      instagram: new Date(),
    },
    commonDate: new Date(),
  });

  const placeCaretAtEnd = (el) => {
    el.focus();
    if (
      typeof window.getSelection != "undefined" &&
      typeof document.createRange != "undefined"
    ) {
      var range = document.createRange();
      range.selectNodeContents(el);
      range.collapse(false);
      var sel = window.getSelection();
      sel.removeAllRanges();
      sel.addRange(range);
    } else if (typeof document.body.createTextRange != "undefined") {
      var textRange = document.body.createTextRange();
      textRange.moveToElementText(el);
      textRange.collapse(false);
      textRange.select();
    }
  };

  const hashtag = (text) => {
    var repl = text.target.textContent.replace(
      /#(\w+)/g,
      `<span class=${classes.Highlight}>#$1</span>`
    );

    let t = text.target;

    t.innerHTML = repl;

    placeCaretAtEnd(t);
  };

  const onSubmit = () => {
    setLoading({ ...loading, success: false });

    const formData = new FormData();
    const data = JSON.stringify(postData);

    formData.append("postData", data);

    formData.append("photo", postData.imageData, postData.imageData.name);

    Axios.post(`${API}/post/create-post`, formData, {
      headers: {
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "multipart/form-data",
        Accept: "multipart/form-data",
        "X-BusinessId": localStorage.getItem("businessId"),
      },
      onUploadProgress: (progressEvent) => {
        setLoading({
          ...loading,
          show: true,
          percentage: Math.round(
            (progressEvent.loaded / progressEvent.total) * 100
          ),
        });
      },
    })
      .then((data) => {
        if (data.data.error) {
          return setErrorMessage(data.data.error);
        }
        return setLoading({
          ...loading,
          show: false,
          percentage: 0,
          success: true,
        });
      })
      .catch((err) => {
        setLoading({ ...loading, show: false, percentage: 0, success: false });
        return setErrorMessage(err.message);
      });
  };

  const loaderContainer = useCallback(
    (percent) => {
      return (
        <>
          <Backdrop show={true} />
          <div className={classes.Loader_Container}>
            <h1>Uploading {loading.percentage}%</h1>
            <Line
              className={classes.Loader_Line}
              percent={percent}
              strokeWidth="14"
              trailWidth="5"
              strokeColor="#12B0EE"
              trailColor="#12B0EE"
            />
          </div>
        </>
      );
    },
    [loading.percentage]
  );

  return (
    <>
      {loading.success && <Scheduled image={postData.imageData} />}
      {loading.success === false && (
        <motion.section className={classes.CreatePost}>
          <Header
            title="Create a Post"
            goBack={() => props.history.goBack()}
            disabled={postData.imageData === null}
            onSubmit={onSubmit}
          />
          {loading.show ? loaderContainer(loading.percentage) : null}
          <div className={classes.SVG_Container} id="SVG-Container">
            {!postData.imageData ? (
              <div className={classes.Upload_Handler}>
                <h3>Upload a photo</h3>
                <form>
                  <input
                    accept="image/*"
                    type="file"
                    onChange={(e) => {
                      return setPostData({
                        ...postData,
                        imageData: e.target.files[0],
                      });
                    }}
                  />
                </form>
              </div>
            ) : (
              <div className={classes.Upload_Btn}>
                <img src={URL.createObjectURL(postData.imageData)} alt="post" />
                <button>
                  Replace Photo{" "}
                  <form>
                    <input
                      accept="image/*"
                      type="file"
                      onChange={(e) => {
                        return setPostData({
                          ...postData,
                          imageData: e.target.files[0],
                        });
                      }}
                    />
                  </form>
                </button>
              </div>
            )}
          </div>
          <div className={classes.Post_Container}>
            <div
              className={classes.Input_Container}
              onClick={(e) => {
                return e.target.textContent === "Write a post description"
                  ? (e.target.textContent = "")
                  : null;
              }}
              onInput={(e) => {
                hashtag(e);
                setPostData({
                  ...postData,
                  copyData: e.target.textContent,
                  twitterCopy: e.target.textContent,
                });
              }}
              contentEditable={true}
            >
              {postData.copyData.length === 0 && "Write a post description"}
            </div>
            {postData.copyData.length > 279 && (
              <p className={classes.TwitterWarn}>
                {postData.twitterCopy.length}/280 !Twitter word limit is
                breached
              </p>
            )}
          </div>

          {postData.advance === false && (
            <div className={classes.Schedule_Container}>
              <h1>Select posting time</h1>

              <div className={classes.Date_Container}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <DateTimePicker
                    required
                    onChange={(data) =>
                      setPostData({
                        ...postData,
                        commonDate: data,
                        scheduledAt: {
                          facebook: data,
                          instagram: data,
                          twitter: data,
                          linkedIn: data,
                        },
                      })
                    }
                    autoOk
                    className={classes.DatePicker}
                    value={postData.commonDate}
                    format="dd/MM/yyyy hh:mm a"
                  />
                </MuiPickersUtilsProvider>

                <div className={classes.Icons}>
                  <img src={facebook} alt="schedule" />
                  <img src={instagram} alt="schedule" />
                  <img src={twitter} alt="schedule" />
                  <img src={linkedIn} alt="schedule" />
                </div>
              </div>

              {/* {postData.advance === false && ( */}
              <div
                className={classes.Advance_Schedule}
                onClick={() =>
                  setPostData({ ...postData, advance: !postData.advance })
                }
              >
                <img src={setting} alt="settings" />
                <p>Advance Scheduling</p>
              </div>
              {/* )} */}
            </div>
          )}

          {postData.advance && (
            <motion.div className={classes.Advance_Schedule_Container}>
              <h1>Select posting time </h1>
              <div className={classes.Advance_Schedule_Form}>
                <form>
                  {/* Facebook */}
                  <div className={classes.Form_Child}>
                    <Checkbox
                      checked={postData.postDestination.facebook}
                      color="primary"
                      onChange={(e) =>
                        setPostData({
                          ...postData,
                          postDestination: {
                            ...postData.postDestination,
                            facebook: e.target.checked,
                          },
                        })
                      }
                      inputProps={{ "aria-label": "secondary checkbox" }}
                    />
                    <div className={classes.Media_Image}>
                      <img src={facebook} alt="facebook" />
                    </div>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <TimePicker
                        required
                        onChange={(data) =>
                          setPostData({
                            ...postData,
                            scheduledAt: {
                              ...postData.scheduledAt,
                              facebook: data,
                            },
                          })
                        }
                        autoOk
                        className={classes.DatePicker}
                        value={postData.scheduledAt.facebook}
                        format="hh:mm a"
                      />
                    </MuiPickersUtilsProvider>
                  </div>

                  {/* Instagram */}
                  <div className={classes.Form_Child}>
                    <Checkbox
                      checked={postData.postDestination.instagram}
                      color="primary"
                      onChange={(e) =>
                        setPostData({
                          ...postData,
                          postDestination: {
                            ...postData.postDestination,
                            instagram: e.target.checked,
                          },
                        })
                      }
                      inputProps={{ "aria-label": "secondary checkbox" }}
                    />
                    <div className={classes.Media_Image}>
                      <img src={instagram} alt="facebook" />
                    </div>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <TimePicker
                        required
                        onChange={(data) =>
                          setPostData({
                            ...postData,
                            scheduledAt: {
                              ...postData.scheduledAt,
                              instagram: data,
                            },
                          })
                        }
                        autoOk
                        className={classes.DatePicker}
                        value={postData.scheduledAt.instagram}
                        format="hh:mm a"
                      />
                    </MuiPickersUtilsProvider>
                  </div>

                  {/* LinkedIn */}
                  <div className={classes.Form_Child}>
                    <Checkbox
                      checked={postData.postDestination.linkedIn}
                      color="primary"
                      onChange={(e) =>
                        setPostData({
                          ...postData,
                          postDestination: {
                            ...postData.postDestination,
                            linkedIn: e.target.checked,
                          },
                        })
                      }
                      inputProps={{ "aria-label": "secondary checkbox" }}
                    />
                    <div className={classes.Media_Image}>
                      <img src={linkedIn} alt="linkedIn" />
                    </div>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <TimePicker
                        required
                        onChange={(data) =>
                          setPostData({
                            ...postData,
                            scheduledAt: {
                              ...postData.scheduledAt,
                              linkedIn: data,
                            },
                          })
                        }
                        autoOk
                        className={classes.DatePicker}
                        value={postData.scheduledAt.linkedIn}
                        format="hh:mm a"
                      />
                    </MuiPickersUtilsProvider>
                  </div>

                  {/* Twitter */}
                  <div className={classes.Form_Child}>
                    <Checkbox
                      checked={postData.postDestination.twitter}
                      color="primary"
                      onChange={(e) =>
                        setPostData({
                          ...postData,
                          postDestination: {
                            ...postData.postDestination,
                            twitter: e.target.checked,
                          },
                        })
                      }
                      inputProps={{ "aria-label": "secondary checkbox" }}
                    />
                    <div className={classes.Media_Image}>
                      <img src={twitter} alt="twitter" />
                      {postData.twitterCopy.length > 279 && (
                        <img
                          className={classes.Alarm}
                          src={alarm}
                          alt="alarm"
                        />
                      )}
                    </div>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <TimePicker
                        required
                        onChange={(data) =>
                          setPostData({
                            ...postData,
                            scheduledAt: {
                              ...postData.scheduledAt,
                              twitter: data,
                            },
                          })
                        }
                        autoOk
                        className={classes.DatePicker}
                        value={postData.scheduledAt.twitter}
                        format="hh:mm a"
                      />
                    </MuiPickersUtilsProvider>
                    {postData.twitterCopy.length > 279 && (
                      <div className={classes.TwitterText_Container}>
                        <p>Word limit {postData.twitterCopy.length}/280</p>
                        <h6 onClick={() => showTwitterInput(!twitterInput)}>
                          Edit twitter Text
                        </h6>
                      </div>
                    )}
                  </div>
                </form>

                {twitterInput && (
                  <div
                    className={classes.Input_Container}
                    onClick={(e) => {
                      return e.target.textContent === "Write a post description"
                        ? (e.target.textContent = " ")
                        : null;
                    }}
                    onInput={(e) => {
                      hashtag(e);
                      setPostData({
                        ...postData,
                        twitterCopy: e.target.textContent,
                      });
                    }}
                    contentEditable={true}
                  >
                    {postData.twitterCopy.length === 0
                      ? "Write a post description"
                      : postData.twitterCopy}
                  </div>
                )}
              </div>
              <div
                className={classes.Basic}
                onClick={() =>
                  setPostData({ ...postData, advance: !postData.advance })
                }
              >
                <p>Basic Scheduling </p> <img src={upArrow} alt="ups" />
              </div>
            </motion.div>
          )}
        </motion.section>
      )}
    </>
  );
};

export default CreatePost;
