/** @format */

import React from "react";
import classes from "./Header.module.css";
const Header = ({ title, goBack, description, disabled, onSubmit }) => {
  return (
    <header className={classes.Header}>
      <div className={classes.LeftArrow} onClick={() => goBack()}>
        <svg
          width="9"
          height="14"
          viewBox="0 0 9 14"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M8 1L1.8858 6.24074C1.42019 6.63984 1.42019 7.36016 1.8858 7.75926L8 13"
            stroke="black"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="bevel"
          />
        </svg>
      </div>

      <div className={classes.Header_Text}>
        <div className={classes.Title}>
          <h1>{title}</h1>
        </div>
        <div className={classes.Sub}>
          <p>{description}</p>
        </div>
      </div>

      <div className={classes.Next}>
        <button disabled={disabled} onClick={() => onSubmit()}>
          Next{" "}
          <svg
            width="9"
            height="11"
            viewBox="0 0 9 14"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M8 1L1.8858 6.24074C1.42019 6.63984 1.42019 7.36016 1.8858 7.75926L8 13"
              stroke="white"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="bevel"
            />
          </svg>
        </button>
      </div>
    </header>
  );
};

export default Header;
